<template>
   <nav>
       <div class="box" v-if="this.$parent.LoginState">
           <span class="userId">{{username}}</span>
           <span class="outLogin" @click="Outlogin">退出</span>
       </div>
       <div v-else class="box btn">
           <router-link to="/login" tag="button" >登录</router-link>
       </div>
        <ul>
            <router-link to="/" tag="li" exact>首页</router-link>
            <router-link to="/myblog" tag="li" exact>我的博客</router-link>
            <router-link to="/add" tag="li" v-if="this.$parent.LoginState">写博客</router-link>
        </ul>

   </nav>
</template>

<script>
   export default {
       name:'',
       data(){
            return{
                username:'',
                LoginState:false
            }
       },
       methods: {
         Outlogin(){
            Bmob.User.logout();
            this.$router.push({path:'/OutLogin'})
         }
       },
       created(){
            let current = Bmob.User.current()
            if(current!=null){
                this.username=current.username
                LoginState=true
            }else{
                LoginState=false
                this.$router.push({path:'/'})
            }
       }
      

   }
</script>

<style scoped>
    *{
        box-sizing: border-box;
    }
    ul{
        list-style: none;
        text-align: center;
        max-width:800px;
        margin: 0 auto;
        box-sizing: border-box;
    }
    li{
        display: inline-block;
        margin: 0 10px;
        color: #ccc;
        padding: 12px;
        /* width: 50px; */
        border-radius: 5px;
        cursor: pointer;
    }
    nav{
        background: crimson;
        padding: 30px 0;
        margin-bottom: 40px;
        position: relative;
    }
    .router-link-active{
        background: rgba(255,255,255,0.8);
        color: #444
    }
    .user-info{
        position: absolute;
    }
    .iconfont{
        font-size: 12px;
        margin-left: 10px;
        cursor:pointer;
    }
    .box{
        position: absolute;
        left: 30px;
        top: 50%;
        transform: translate(0,-50%) 
    }
    .outLogin{
        display: inline-block;
        width: 80px;
        height: 30px;
        background: rgba(206, 205, 205, 0.288);
        border-radius: 4px;
        text-align: center;
        line-height: 30px;
        color: #eee;
        margin-left: 10px;
        cursor: pointer;
    }
    #myicon{
        position: absolute;
        left: 0;
        bottom: -150%;
    }
    .btn button{
        padding: 10px 25px;
        border: none;
        cursor: pointer;
        color: #000;
        border-radius: 4px;
        background: rgba(255, 255, 255, 0.726);
    }
    .userId{
        font-size: 24px;
    }
    
</style>