<template>
   <div id='Show-Blogs'>
       <transition name="anima">
            <div v-if="ShowHeader">
                <h1>博客总览</h1>
                <input type="text" placeholder="搜索" v-model="search">
            </div>
        </transition>

        <transition name="anima">
            <div v-if="ShowList">
            <div class="single-blog NotList" v-if="NotList">
                暂无数据
            </div>
                <div  v-for="blog in filteredBlogs" :key="blog.id" class="single-blog">
                    <router-link :to="'/blog/'+blog.objectId">
                            <h2 v-rainbow>{{blog.title | toUppercase}}</h2>
                    </router-link>
                    <article>
                        {{blog.content | snippet}}
                    </article>
                    <p class="ShowTime">  发表于：{{ blog.updatedAt }}</p>
                </div>
            
            </div>
        </transition>
        <div class="list" v-if="showPaging">
           <ul>
               <input maxlength="3" type="number" min="1" id="goto" placeholder="Enter" @keydown.enter="goto()">
                <span class="iconfont left" @click="prev(activeClass-1)">&#xe615;</span>
               <li v-for="(item,index) in showListNum" :key="index" @click="goto(item)" :class="{active:item==activeClass}">{{item}}</li>
                <span class="iconfont right" @click="next(activeClass+1)">&#xe61a;</span>
           </ul>
        </div>
   </div>
</template>

<script>
   export default {
       name:'show-blogs',
       data(){
            return{
               blogs:[],
               search:'',
               ShowHeader:false,
               ShowList:false,
               NotList:false,
               BlogLength:null,
               activeClass:1,
               ListNum:[],
               showListNum:[],
               end:Number,
               start:Number,
               showPaging:false
            }
       },
       computed:{

       },
       
       created(){
            this.ShowHeader=true;
            const query = Bmob.Query("BlogList");
            query.limit(4);
            query.find().then(res => {
                if(res.length>0){
                     this.blogs=res;
                     this.blogs.reverse();
                     this.ShowList=true;
                }else{
                    this.ShowList=true;
                    this.NotList=true;
                }
               
            });
            const query2 = Bmob.Query("BlogList");
                query2.find().then(res => {
                    // this.ListNum=Array.from(res);
                    this.BlogLength=Math.ceil(res.length/4)
                    for(let i=1;i<this.BlogLength+1;i++){
                        this.ListNum.push(i)
                    }
                    this.showListNum=this.ListNum.slice(0,4)
                    this.showPaging=true;
            });
       },

    //    1 =>0  2=>4 3=> 8 4=>12  (item-1)*4 
       methods:{
           next(item){
               console.log(item)
            //    console.log(this.ListNum.slice(item-1,item+4))
                if(item>this.BlogLength){
                    item=this.BlogLength
                    return 
                }
                if(item%4==1){
                    console.log(this.ListNum)
                    this.showListNum=this.ListNum.slice(item-1,item+3)
                }
                let num=4*item-4
                this.activeClass=item;
                this.ShowHeader=true;
                const query = Bmob.Query("BlogList");
                query.limit(4);
                query.skip(num);
                query.find().then(res => {
                    if(res.length>0){
                        this.blogs=res;
                        this.blogs.reverse();
                        this.ShowList=true;
                    }else{
                        this.ShowList=true;
                        this.NotList=true;
                    }
                
                });
           },
           prev(item){
                if(item!=1&&item%4==0&&item!=0){
                    this.showListNum=this.ListNum.slice(item-4,item)
                    console.log(this.showListNum)
                }
                if(item==0){
                    item=1;
                }
                this.activeClass=item;
                let num=4*item-4
                this.ShowHeader=true;
                const query = Bmob.Query("BlogList");
                query.limit(4);
                query.skip(num);
                query.find().then(res => {
                    if(res.length>0){
                        this.blogs=res;
                        this.blogs.reverse();
                        this.ShowList=true;
                    }else{
                        this.ShowList=true;
                        this.NotList=true;
                    }
                
                });
           },
           //20 21 22 23
           goto(index){
               var item;
                if(index==undefined){
                    console.log("这里是index==undefined")
                    if(event.target.value*1>this.ListNum.length){
                        item=this.ListNum.length;
                    }
                    else if(event.target.value*1<1){
                        item=1;
                    }
                    else{
                        item=event.target.value*1;
                    }
                }else{
                    console.log("这里是index==undefined的else")
                    item=index;
                }
               
                if(this.ListNum.length-item<4){
                    
                    console.log("这里是this.ListNum.length-item<4")
                    if(!this.showListNum.includes(item*1)){
                        console.log("这里是!this.showListNum.includes(item*1)")
                        let z=this.ListNum.length-item
                        this.showListNum=this.ListNum.slice(item-2,this.ListNum.length)
                    }
                    // console.log(this.showListNum.includes(item*1))
                }
                else{
                    // console.log(this.ListNum.length)
                     console.log("这里是this.ListNum.length-item<4的else")
                    for(let i =item;i<this.ListNum.length;i++){
                        if(i%4==0){
                            this.end=i;
                            break
                        }
                    }
                    // for(let i =item;i<this.ListNum.length;i--){
                    //     if(i%4==){
                    //         this.start=i;
                    //         break
                    //     }
                    // }
                    this.showListNum=this.ListNum.slice(this.end-4,this.end)
                }
                

                this.activeClass=item;
                let num=4*item-4
                this.ShowHeader=true;
                const query = Bmob.Query("BlogList");
                query.limit(4);
                query.skip(num);
                query.find().then(res => {
                    if(res.length>0){
                        this.blogs=res;
                        this.blogs.reverse();
                        this.ShowList=true;
                    }else{
                        this.ShowList=true;
                        this.NotList=true;
                    }
                
                });
           },

       },
       computed:{
           filteredBlogs(){
               return this.blogs.filter((blog)=>{
                   return blog.title.match(this.search)
               })
           },
          
       },
       filters:{
           toUppercase(value){
               return value.toUpperCase();
           },
           snippet(value){
               return value.slice(0, 100) + '...';
           },
       },
       directives:{
           'rainbow':{
               bind(el,binding,vnode){
                   el.style.color="#"+Math.random().toString(16).slice(2,8)
               }
           }
       }
   }
</script>

<style scoped>
    h1{
        margin-bottom: 20px;
    }
    #Show-Blogs{
        max-width: 800px;
        margin:  0 auto;
        padding:0 30px;
        position:relative;
        height: 700px;
        
    }
    .single-blog{
        padding: 20px;
        margin: 20px 0;
        box-sizing: border-box;
        background: #eee;
        border: 1px dotted #aaa;
    }

    #Show-Blogs a{
        color:#444;
        text-decoration: none;
    }

    input[type='text']{
        padding: 8px;
        width: 100%;
        box-sizing: border-box;
    }
    .ShowTime{
        font-size: 14px;
        color:rgb(128, 128, 128);
        text-align: right;
        
    }
    .NotList{
        text-align: center;
    }
    .top{
        text-align: right;
        margin-bottom: 10px;
    }

    .iconfont{
        color: #7a7a7a;
        cursor: pointer;
        font-size: 16px
    }

    .anima-enter-active {
        animation: anima-in .2s;
    }
    .anima-leave-active {
        animation: anima-in .2s reverse;
    }
    @keyframes anima-in {
        0% {
            opacity: 0;
        }
        50% {
            opacity: 0.5;
        }
        100% {
            opacity: 1;
        }
    }
    .list *{
        user-select:none; 
    }
    .list ul{
        padding: 0 30px;
        right: 0;
        height: 30px;
        position: absolute;
        bottom: 0;
    }

    .list ul li{
        background: #eee;
        display: inline-block;
        width: 30px;
        height: 30px;
        text-align: center;
        line-height: 30px;
        margin-left: 10px;
        cursor: pointer;
    }

    .active{
        background: #ff9c9c !important;
    }
     .left{
        margin-right: 10px;
    }
    .right{
         margin-left: 20px;
    }
    #goto{
        padding: 0;
        width: 40px;
        height: 100%;
        text-align: center;
        /* float: right; */
        margin-right: 20px;
        
    }


</style>