<template>
   <div id='register'>
       <div class="wrap">
            <div v-if="!showBox" class="box">
                    <h2>用户注册</h2>
                    <div class="row user">
                        <label for="">账号：</label>
                        <input type="text" v-model="user" @blur="checkUser">
                        <label class="erro-info" v-show="checkInfo">账号已注册</label>
                    </div>
                    <div class="row password">
                        <label for="">密码：</label>
                        <input type="text" v-model="password">
                    </div>
                    <input type="button" value="注册" @click="register">
                </div>
                <div v-if="showBox">
                    <h3>注册成功! <router-link to="/login">现在登录</router-link> </h3>  
                </div>
       </div>
       
   </div>
</template>

<script>
   export default {
       name:'register',
       data(){
            return{
               user:'',
               password:'',
               showBox:false,
               checkInfo:null
            }
       },
       methods:{
           register(){
                if(this.checkInfo==false&&this.user!=''&&this.password!=''){
                   let params = {
                        username: this.user,
                        password: this.password,
                    }
                    Bmob.User.register(params).then(res => {
                        this.showBox=true

                    }).catch(err => {
                        console.log(err)
                    });
                }else{
                    return
                }
           },
           checkUser(event){
                Bmob.User.users().then(res => {
                let len=len=res.results.length;
                let val=event.target.value;
                for(let i=0;i<len;i++){
                    if(res.results[i].username==val){
                        console.log('用户名已注册');
                        this.checkInfo=true;
                        return;
                    }
                    else{
                        this.checkInfo=false;
                    }
                }
                }).catch(err => {
                console.log(err)
                })
           }
       }
   }
</script>

<style scoped>
    #register{
        max-width: 500px;
        height: 300px;
        background: #eee;
        margin: 100px auto;
        border-radius: 5px;
        border: 1px dotted #ccc;
        position: relative;
        box-sizing: border-box;
    }
    .wrap{
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%,-50%);

    }
    input[type='button']{
        padding:10px 25px;
        display: block;
        float: right;
        border: none;
        font-size: 14px;
        font-weight: normal;
        background: crimson;
        border-radius: 4px;
        color: #fff;

    }
    input[type='text']{
        height: 20px;
        border-radius: 5px;
        border: 1px solid #ccc;
        background: #fff;
        padding: 5px;
    }
    label{
        font-size: 16px;
    }
    .row{
        margin-bottom: 20px;
        position: relative;
    }
    h2{
        color:rgb(83, 83, 83);
        text-align: center;
        margin-bottom: 15px;
    }
    .erro-info{
        position: absolute;
        box-sizing: border-box;
        right: -70px;
        color: red;
        /* padding: 5px; */
        font-size: 13px;
        line-height: 30px;
      
        
    }

</style>