<template>
   <div id='add-blog'>
       <h2>添加博客</h2>
       <form action="" v-if="!submited">

           <label for="">博客标题</label>
           <input type="text" v-model="blog.title" required>

           <label for="">博客内容</label>
           <textarea v-model="blog.content"></textarea>

           <div id="checkboxes">
               
               <input v-model="blog.categories" type="checkbox" value="Vue.js">
               <label for="">Vue.js</label>
               
               <input v-model="blog.categories" type="checkbox" value="Node.js">
               <label for="">Node.js</label>
               
               <input v-model="blog.categories" type="checkbox" value="React.js">
               <label for="">React.js</label>
               
               <input v-model="blog.categories" type="checkbox" value="Angular4">
               <label for="">Angular4</label>
           </div>

            <button v-on:click.prevent="post">添加博客</button>
       </form>

       <div v-if="submited">
           <h3>您的博客发布成功</h3>
           <h4>{{ time.createdAt }}</h4>
       </div>
       
       
       <button @click="home" v-if="submited">返回首页</button>
   </div>
   
   
</template>

<script>
   export default {
       name:'add-blog',
       data(){
            return{
               blog:{
                   title:'',
                   content:'',
                   categories:[],
               },
               time:Object,
               submited:false
            }
       },
       methods: {
           post(){
                let current = Bmob.User.current()
                const query = Bmob.Query('BlogList');
                query.set("title",this.blog.title)
                query.set("content",this.blog.content)
                query.set("categories",this.blog.categories)
                query.set("author",current.username)
                query.set("time",new Date().getTime())
                 query.set("top",0)
                query.save().then(data => {
                    console.log(data)
                    this.time=data;
                    this.submited=true;
                }).catch(err => {
                    console.log(err)
                })
           },
           home(){
                this.$router.push({path:'/'})
           }
       },
   }
</script>

<style scoped>
    #add-blog *{
        box-sizing: border-box;
    }
    #add-blog{
        margin: 20px auto;
        max-width: 600px;
        padding: 20px;
    }
    label{
        display: block;
        margin: 20px 0 10px;
    }
    input[type="text"],textarea,select{
        display: block;
        width: 100%;
        padding: 8px;
        color: #5c5c5c;
        font-size: 18px;
        font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif
    }
     #checkboxes input[type="checkbox"]{
        /* margin-left: 5px; */
         margin-right: 5px !important;
         margin-left: 20px;
    }
    textarea{
        height: 200px;
        resize: none;
    }
    #checkboxes label{
        display: inline-block;
        margin-top: 5px;
    }
    #checkboxes input{
        display: inline-block;
        margin-right: 25px;
    }
    button{
        display: block;
        margin: 20px 0;
        background: crimson;
        color: #fff;
        border: 0;
        padding: 10px;
        border-radius: 4px;
        cursor: pointer;
        font-size: 16px;
        float: right;

    }
    #preview{
        padding: 10px 20px;
        border: 1px dotted #ccc;
        margin: 30px 0;
    }
    h3{
        margin-top: 10px;
    }
    h4{
        text-align: right
    }
    .anima-enter-active {
        animation: anima-in .2s;
    }
    .anima-leave-active {
        animation: anima-in .2s reverse;
    }
    @keyframes anima-in {
        0% {
            opacity: 0;
        }
        50% {
            opacity: 0.5;
        }
        100% {
            opacity: 1;
        }
    }
</style>