<template>
   <div id='login' >
       <div class="wrap">
           <h2>用户登录</h2>
            <div class="row user">
                <label for="">账号：</label>
                <input type="text" v-model="user">
                <router-link to="/register" tag="label" class="erro-info">注册</router-link>
            </div>
            <div class="row password">
                <label for="">密码：</label>
                <input type="text" v-model="password">
            </div>
            <input type="button" value="登录" @click="login">
       </div>
       
   </div>
</template>

<script>
   export default {
       name:'login',
       data(){
            return{
               user:'',
               password:''
            }
       },
       methods:{
           login(){
               if(this.user!=""&&this.password!=""){
                    Bmob.User.login(this.user,this.password).then(res => {
                       this.$router.push({path:'/'});
                    }).catch(err => {
                        console.log(err)
                        alert("密码错误或账号不存在")
                    });
                }else{
                    return
                }
                
           }
       }
   }
</script>

<style scoped>
    #login{
        max-width: 500px;
        height: 300px;
        background: #eee;
        margin: 100px auto;
        border-radius: 5px;
        border: 1px dotted #ccc;
        position: relative;
        box-sizing: border-box;
    }
    .wrap{
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%,-50%)
    }
    input[type='button']{
        padding:10px 25px;
        display: block;
        float: right;
        border: none;
        font-size: 14px;
        font-weight: normal;
        background: crimson;
        border-radius: 4px;
        color: #fff;

    }
    input[type='text']{
        height: 20px;
        border-radius: 5px;
        border: 1px solid #ccc;
        background: #fff;
        padding: 5px;
    }
    label{
        font-size: 16px;
    }
    .row{
        margin-bottom: 20px;
    }
    h2{
        color:rgb(83, 83, 83);
        text-align: center;
        margin-bottom: 15px;
    }
    a{
        text-decoration: none;
        color:#000;
        display: inline-block;
        background: #ccc;
        padding: 10px 15px;
    }
    .erro-info{
        position: absolute;
        box-sizing: border-box;
        right: -35px;
        color: rgb(17, 72, 223);
        /* padding: 5px; */
        font-size: 13px;
        line-height: 30px;
        cursor: pointer;
        
    }
    #info{
        font-size: 12px;
    }
</style>