<template>
   <div id='Show-Blogs'>
       <transition name="anima">
            <div v-if="ShowHeader">
                <h1>我的博客</h1>
                <input type="text" placeholder="搜索" v-model="search">
            </div>
        </transition>

        <transition name="anima">
        <div v-if="ShowList">
            <div class="single-blog NotList" v-if="info">
                {{msg}}
            </div>
            <div  v-for="blog in filteredBlogs" :key="blog.id" class="single-blog">
                <router-link :to="'/blog/'+blog.objectId">
                        <h2 v-rainbow>{{blog.title | toUppercase}}</h2>
                </router-link>
                <article>
                    {{blog.content | snippet}}
                </article>
                <p class="ShowTime">发表于：{{ blog.updatedAt }}</p>
            </div>
        </div>
        </transition>
       
   </div>
</template>

<script>
   export default {
       name:'show-blogs',
       data(){
            return{
               blogs:[],
               search:'',
               ShowHeader:false,
               ShowList:false,
               info:false,
               msg:''
            }
       },
       computed:{
           ShowTime(){
               
           }
       },
       created(){
            this.ShowHeader=true;
            let current = Bmob.User.current()

            // console.log(current)

            if(current!=null){
                const query = Bmob.Query("BlogList");
                query.equalTo("author","==", current.username);
                query.find().then(res => {
                    if(res.length>0){
                        this.blogs=res;
                        this.blogs.reverse();
                        this.ShowList=true;
                    }else{
                        this.ShowList=true;
                        this.info=true;
                         this.msg="你还没发布过博客"
                    }
                
                });
            }else{
                this.ShowList=true;
                this.info=true;
                this.msg="请登录"
            }
       },
       methods:{
       },
       computed:{
           filteredBlogs(){
               return this.blogs.filter((blog)=>{
                   return blog.title.match(this.search)
               })
           },
       },
       filters:{
           toUppercase(value){
               return value.toUpperCase();
           },
           snippet(value){
               return value.slice(0, 100) + '...';
           },
       },
       directives:{
           'rainbow':{
               bind(el,binding,vnode){
                   el.style.color="#"+Math.random().toString(16).slice(2,8)
               }
           }
       }
   }
</script>

<style scoped>
    h1{
        margin-bottom: 20px;
    }
    #Show-Blogs{
        max-width: 800px;
        margin:  0 auto;
        padding:0 30px
        
    }
    .single-blog{
        padding: 20px;
        margin: 20px 0;
        box-sizing: border-box;
        background: #eee;
        border: 1px dotted #aaa;
    }

    #Show-Blogs a{
        color:#444;
        text-decoration: none;
    }

    input[type='text']{
        padding: 8px;
        width: 100%;
        box-sizing: border-box;
    }
    .ShowTime{
        font-size: 14px;
        color:rgb(128, 128, 128);
        text-align: right;
        
    }
    .NotList{
        text-align: center;
    }

    .anima-enter-active {
        animation: anima-in .2s;
    }
    .anima-leave-active {
        animation: anima-in .2s reverse;
    }
    @keyframes anima-in {
        0% {
            opacity: 0;
        }
        50% {
            opacity: 0.5;
        }
        100% {
            opacity: 1;
        }
    }



</style>