<template>
   <div id='single-blog'>
       <h1>{{ blog.title }}</h1>
       <article>{{blog.content}}</article>
       <div class="footer">
            <p class="author">作者：{{ blog.author }}</p>
            <ul>
                <li v-for="category in blog.categories" :key="category">
                   <span id="myicon" class="iconfont icon-xxx">&#xe6a9;</span>{{ category }}
                </li>
            </ul>
            <p class="time">发表于：{{blog.updatedAt}}</p>
            <div v-if="permissions">
                 <router-link :to="'/edie/'+id" tag="input" type="button" value="编辑"></router-link>
                <input @click="DeleteBlog" type="button" value="删除">
            </div>
           
       </div>
   </div>
</template>
<style scoped>
    #single-blog{
        max-width: 960px;
        margin: 0 auto;
        padding: 20px;
        background: #eee;
        border: 1px dotted #aaa;
    }
    h1{
        text-align: center;
    }
    article{
        text-indent: 2em;
        font-size: 18px;
        line-height: 30px;
        padding-top: 15px;
    }
    .footer{
        padding-top: 30px;
        text-align: right;
    }
    .footer ul{
        list-style: none;
        margin-bottom: 10px;
    }
    .footer ul li{
        display: inline-block;
        margin-left: 30px;
        padding: 10px 0;
    }
    .author{
        padding: 10px 0;
    }
    input{
        border: none;
        background: #eee;
        padding: 4px 10px;
        color: #000;
        border: 1px solid #ccc;
        cursor: pointer;
    }
    input:hover{
        background: #fff;
    }
    #myicon{
        color: rgb(109, 109, 109);
        font-size: 14px;
        margin-right: 5px;
    }
    .time{
        margin-bottom: 20px;
    }
</style>
<script>
   export default {
       name:'single-blog',
       data(){
            return{
               id:this.$route.params.id,
               blog:{},
               permissions:false
            }
       },
       created(){
            let current = Bmob.User.current()
            console.log(current)
            const query = Bmob.Query('BlogList');
            query.get(this.id).then(res => {
                this.blog=res;
                if(res.author==current.username){
                    console.log("有权限")
                    this.permissions=true;
                }
            }).catch(err => {
                console.log(err)
            })
       },
       methods:{
           DeleteBlog(){
                // let current = Bmob.User.current()
                // console.log(current.id,this.blog.id);
                if(this.permissions){
                    const query = Bmob.Query('BlogList');
                    query.destroy(this.id).then(res => {
                        alert("删除成功")
                        this.$router.push({path:'/'})
                    }).catch(err => {
                        console.log(err)
                    })
                }
                
           }
       }
   }
</script>

