import Vue from 'vue'
import Router from 'vue-router'
import ShowBlogs from './components/ShowBlogs.vue'
import AddBlog from './components/AddBlog.vue'
import SingleBlog from './components/SingleBlog.vue'
import EditBlog from './components/EditBlog.vue'
import Login from './components/Login.vue'
import Register from './components/Register.vue'
import OutLogin from './components/OutLogin.vue'
import MyBlog from './components/MyBlog.vue'
Vue.use(Router)

export default new Router({
  mode: "history",
  routes: [
    {
      path: '/',
      name: 'ShowBlogs',
      component: ShowBlogs
    },
    {
      path: '/add',
      name: 'AddBlog',
      component: AddBlog
    },
    {
      path: '/blog/:id',
      name: 'SingleBlog',
      component: SingleBlog
    },
    {
      path: '/edie/:id',
      name: 'EditBlog',
      component: EditBlog
    },
    {
      path: '/login',
      name: 'Login',
      component: Login
    },
    {
      path: '/register',
      name: 'Register',
      component: Register
    },
    {
      path: '/OutLogin',
      name: 'OutLogin',
      component: OutLogin
    }, 
    {
      path: '/myblog',
      name: 'MyBlog',
      component: MyBlog
    },
  ],

})
