<template>
   <div id='Edit-blog'>
       <h2>编辑博客</h2>
       <form action="" v-if="!submited">

           <label for="">博客标题</label>
           <input type="text" v-model="blog.title" required>

           <label for="">博客内容</label>
           <textarea v-model="blog.content"></textarea>

           <div id="checkboxes">
               <label for="">Vue.js</label>
               <input v-model="blog.categories" type="checkbox" value="Vue.js">
               <label for="">Node.js</label>
               <input v-model="blog.categories" type="checkbox" value="Node.js">
               <label for="">React.js</label>
               <input v-model="blog.categories" type="checkbox" value="React.js">
               <label for="">Angular4</label>
               <input v-model="blog.categories" type="checkbox" value="Angular4">
           </div>

           <label for="">作者:</label>
           <select v-model="blog.author">
               <option v-for="author in authors" :key="author">
                   {{author}}
               </option>
           </select>
            <button v-on:click.prevent="Edit">提交编辑</button>
       </form>

       <div v-if="submited">
           <h3>您的博客编辑成功</h3>
           <h4>{{ time.updatedAt }}</h4>
       </div>
       <button @click="home" v-if="submited">返回首页</button>

   </div>
</template>

<script>
   export default {
       name:'Edit-blog',
       data(){
            return{
               blog:{
                   title:'',
                   content:'',
                   categories:[],
                   author:''
               },
               time:Object,
               authors:["尤雨溪","阮一峰","曾刚"],
               submited:false,
               id:this.$route.params.id
            }
       },
       methods: {
           Edit(){
               const query = Bmob.Query('BlogList');
               query.set('id', this.id) //需要修改的objectId
               query.set('title', this.blog.title)
               query.set('content', this.blog.content)
               query.set('categories', this.blog.categories)
               query.set('author', this.blog.author)
               query.save().then(res => {
                  this.time=res;
                  this.submited=true
               }).catch(err => {
                  console.log(err)
               })
           },
           home(){
                this.$router.push({path:'/'})
           }
       },
       created(){
            const query = Bmob.Query('BlogList');
            query.get(this.id).then(res => {
                this.blog=res;
            }).catch(err => {
                console.log(err)
            })
       }
   }
</script>

<style scoped>
    #Edit-blog *{
        box-sizing: border-box;
    }
    #Edit-blog{
        margin: 20px auto;
        max-width: 600px;
        padding: 20px;
    }
    label{
        display: block;
        margin: 20px 0 10px;
    }
    input[type="text"],textarea,select{
        display: block;
        width: 100%;
        padding: 8px;
        color: #5c5c5c;
        font-size: 18px;
        font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif
    }
    textarea{
        height: 200px;
    }
    #checkboxes label{
        display: inline-block;
        margin-top: 5px;
    }
    #checkboxes input{
        display: inline-block;
        margin-right: 25px;
    }
    button{
        display: block;
        margin: 20px 0;
        background: crimson;
        color: #fff;
        border: 0;
        padding: 10px;
        border-radius: 4px;
        cursor: pointer;
        font-size: 16px;
        float: right;
    }
    #preview{
        padding: 10px 20px;
        border: 1px dotted #ccc;
        margin: 30px 0;
    }
    h3{
        margin-top: 10px;
    }
    h4{
       text-align: right;
    }
</style>